._post_bar {
  width: 100%;
  display: flex;
  padding: 10px;
  justify-content: space-between;
  /* border-bottom: 1px solid #f1f1f1; */
}

._post_bar div {
  display: flex;
  gap: 20px;
}

._post_bar svg {
  font-size: 1.5em;
  cursor: pointer;
  color: var(--dark-grey);
}

._liked_post svg {
  color: rgb(234, 64, 64);
}
